html {
  height: 100%;
}

body {
  margin: 0;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  /* layout */

  /*   --clr-bg: #0e1212;
  --clr-bg-line: 14, 18, 18; */
  --clr-back: rgb(31, 32, 40);
  --clr-txt: rgb(215, 212, 221);
  --clr-li: rgba(63, 0, 113, 0.1);
  --clr-li2: rgb(97, 0, 148);
  --clr-brand: rgb(145, 107, 191);

  /* --clr-txt: rgb(219, 219, 219); */

  /* card */
  --clr-card-shad: 5px 5px 15px rgb(1 1 1 / 80%);
  --clr-card-back: rgba(105, 94, 147, 0.6);

  /* hover */
  --clr-back-hover: linear-gradient(rgb(96, 63, 139) 65%, rgb(121, 84, 161));
  /* active */
  --clr-back-active: linear-gradient(
    rgba(96, 63, 139, 0.6),
    rgba(121, 84, 161, 0.6)
  );

  /* success */
  --clr-success: rgb(92, 184, 92);

  /* pallet 1 */
  /*
    #1c0c5b
    #3d2c8d
    #916bbf
    #c996cc

  /* pallet 2 */
  /*
    #000000
    #150050
    #3f0071
    #610094
  */

  /* pallet 3 */
  /*
    rgba(219, 216, 227, 1)
    rgba(92, 84, 112, 1)
    rgba(53, 47, 68, 1)
    rgba(42, 36, 56, 1)
  */

  /* pallet 4 */
  /*
    rgba(21, 21, 21, 1)
    rgba(48, 27, 63, 1)
    rgba(60, 65, 92, 1)
    rgba(180, 165, 165, 1)
  */

  /*   --clr-green: rgb(76, 175, 80);
  --clr-green-back: rgba(76, 175, 80, 0.5);
  --clr-yellow: #ffcc70;
  --clr-red: rgb(239, 83, 80);
  --clr-red-back: rgba(239, 83, 80, 0.5);
  --clr-light-gray: #7a92a5;
  --clr-box: #e1e5e8;
  --clr-white: #fff; */
}

:root {
  /* Width */
  --w-100: 100%;
  --w-98: 98%;
  --w-95: 95%;
  --w-90: 90%;
  --w-80: 80%;
  --w-75: 75%;
  --w-70: 70%;
  --w-60: 60%;
  --w-55: 55%;
  --w-50: 50%;
  --w-45: 45%;
  --w-40: 40%;
  --w-35: 35%;
  --w-30: 30%;
  --w-25: 25%;
  --w-20: 20%;
  --w-15: 15%;
  --w-20-vw: 20vw;
  --w-30-vw: 30vw;
  --w-40-vw: 40vw;
  --w-45-vw: 45vw;
  --w-18-vw: 18vw;

  /* Height */
  --h-100: 100%;
  --h-40: 40%;
  --h-35: 35%;
  --h-30: 30%;
  --h-30-px: 30px;
  --h-5-vh: 5vh;
  --h-15-vh: 15vh;

  /* Margin */
  /* **margin-top** */
  --m-top-70: 70%;
  --m-top-30: 30%;
  --m-top-20: 20%;
  --m-top-15: 15%;
  --m-top-50: 50%;
  --m-top-1: 1%;
  --m-top-2: 2%;
  --m-top-5: 5%;

  --m-top-40-px: 40px;
  --m-top-20-px: 20px;
  --m-top-15-px: 15px;
  --m-top-10-px: 10px;

  --m-top-1-rem: 1rem;
  --m-top-s-rem: 0.8rem;
  --m-3-vh: 3vh;

  /* **margin-left** */
  --m-left-50: 50%;
  --m-left-40: 40%;
  --m-left-30: 30%;
  --m-left-25: 25%;
  --m-left-5: 5%;
  --m-left-1: 1%;

  --m-left-10-px: 10px;
  --m-left-au: auto;

  /* **margin-right** */
  --m-right-20: 20px;
  --m-right-40: 40px;
  --m-r-auto: auto;

  /* **margin** */
  --m-auto: auto;
  --m-5: 5%;
  --m-0-1-0-2: 0 1% 0 2%;
  --m-2: 2%;

  /* Padding */
  --pd-1: 1%;
  --pd-1-0: 1% 0;
  --pd-2: 2%;
  --pd-3: 3%;
  --pd-4: 4%;
  --pd-5: 5%;
  --pd-0-1: 0 1%;
  --pd-5-px: 5px;
  --pd-10-px: 10px;

  --pd-media-l: 0 5rem;

  /* Padding-Top */
  --pt-20: 20%;

  /* Border-Width */
  --bor-w-001: 0 0 1px 0;

  /* Border-Style */
  --bor-st-sol: solid;

  /* Border-Radius */
  --br-5-px: 5px;
  --br-15-px: 15px;

  /* Flex */
  --flex: flex;
  /* justify-content*/
  --jc-c: center;
  --jc-se: space-evenly;

  /* align-items */
  --al-c: center;
  /* flex-flow */
  --f-flow-cw: column wrap;
  --f-flow-rw: row wrap;
  /* Inline-Flex */
  --d-in-flex: inline-flex;
}

:root {
  /* Font-Size */
  --fs-basis: 1em; /*16px*/
  --fs-basis-s1: 0.9em;
  --fs-basis-s2: 0.95em;
  --fs-basis-s3: 0.85em;
  --fs-basis-s4: 0.8em;
  --fs-basis-s5: 0.75em;

  --fs-basis-m1: 1.1em;
  --fs-basis-m2: 1.2em;
  --fs-basis-m3: 1.5em;
  --fs-basis-m4: 1.8em;
  --fs-basis-m5: 1.7em;

  --fs-basis-l1: 2.5em;
  --fs-basis-l2: 2em;
  --fs-px-9: 9px;
  --fs-px-11: 11px;
  --fs-px-12: 12px;
  --fs-px-14: 14px;
  --fs-px-15: 15px;
  --fs-px-24: 24px;

  /* Font-Weight */
  --fw-bold: 700;
  --fw-light: 400;
  --fw-lightest: 300;
  --fw-medium: 600;

  /* Font-Family */
  --ff-primary: "Fira Sans", sans-serif;
  --ff-landing: "Balsamiq Sans", cursive;

  /* Letter-Spacing */
  --ls-normal: "normal";
  --ls-s1: 0.05em;
  --ls-s2: 0.07em;
  --ls-l: 0.1em;

  /* Line-Height */
  --lh-2: 2;
  --lh-15: 1.5;

  /* Text-Align */
  --ta-c: center;

  /* Text-Transform */
  --tt-up: uppercase;
  --tt-cap: capitalize;
}

.logo {
  display: flex;
  align-items: center;
  grid-gap: 0.25rem;
  gap: 0.25rem;
  font-size: 1.5em;
  font-family: "Carter One", cursive;
  letter-spacing: 0.08em;
  color: #7954a1;
}

.logo img {
  height: 2.5rem;
  padding-bottom: 0.25rem;
}

.link {
  padding: 0.5rem 1rem;
  border-radius: 5px;
  scroll-behavior: smooth;
}

.btn-like {
  padding: 1rem;
  display: inline-flex;
  align-items: center;
  border: 3px solid #603f8b;
}

.active-link,
.link:hover {
  border-radius: 5px;
  background: var(--clr-back-hover);
  border-color: transparent;
}

.s-nav-link {
  padding: 0.75rem;
}

.left-space {
  margin-left: 2vw;
}

.link-icon {
  font-size: 14px;
  padding-right: 0.5rem;
}

.right {
  padding: 0 0 0 0.5rem;
  font-size: 1.2em;
  transition: -webkit-transform 0.4s;
  transition: transform 0.4s;
  transition: transform 0.4s, -webkit-transform 0.4s;
}

.btn-like:hover .right {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  padding: 0 0.5rem;
}

.link-title {
  font-size: 14px;
}

.link-title-btn {
  font-size: 1.2em;
}

@media screen and (max-width: 800px) {
  .link {
    padding: 0.5rem;
  }

  .btn-like {
    padding: 0.75rem;
  }

  .s-nav-link {
    padding: 0.75rem;
  }

  .s-nav-icon {
    font-size: 1.2em;
    padding-right: 1rem;
  }

  .s-nav-link-title {
    font-size: 1.3em;
  }

  .right {
    font-size: 1em;
  }

  .link-title-btn {
    font-size: 1em;
  }
}

@media screen and (max-width: 500px) {
  .s-nav-icon {
    font-size: 1em;
  }

  .s-nav-link-title {
    font-size: 1.1em;
  }
}

@media screen and (max-width: 400px) {
  .s-nav-link {
    padding: 0.5rem;
  }

  .s-nav-icon {
    font-size: 0.95em;
  }

  .s-nav-link-title {
    font-size: 1em;
  }
}

.navbar-wrapper {
  margin-left: auto;
}

@media screen and (max-width: 700px) {
  .side-navbar {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 3.5rem;
    left: auto;
    transition: 0.5s;
    right: 0;
    z-index: 2;
    background-color: var(--clr-back);
    width: 100%;
    text-align: center;
    padding: 1rem 0;
    grid-gap: 0.25rem;
    gap: 0.25rem;
  }

  .nav-close {
    display: none;
  }
}

.burger-menu-container {
  display: none;
}

.burger-menu-icon {
  font-size: 1em;
}

.burger-menu-wrapper {
  height: 3vh;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}

.burger-menu-line {
  background: var(--clr-back-hover);
  height: 0.5vh;
  width: 2rem;
  align-self: center;
  transition: all linear;
  border-radius: 10px;
}

.close-burger-menu {
  width: 100%;
  height: 2px;
  transition: 0.3s ease;
  -webkit-transform-origin: left;
          transform-origin: left;
}

.close-burger-menu:nth-child(1) {
  -webkit-transform: rotate(40deg);
          transform: rotate(40deg);
  /* transform: translateY(8px) rotate(-315deg); */
}

.close-burger-menu:nth-child(2) {
  /* opacity: 0; */
  -webkit-transform: scale(0);
          transform: scale(0);
}

.close-burger-menu:nth-child(3) {
  -webkit-transform: rotate(-40deg);
          transform: rotate(-40deg);
  -webkit-transform-origin: left;
          transform-origin: left; /* transform: translateY(-10px) rotate(-45deg) */
}

@media screen and (max-width: 700px) {
  .burger-menu-container {
    display: block;
    margin-left: auto;
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    cursor: pointer;
  }

  .menu-active {
    width: 22px;
    height: 16px;
    cursor: pointer;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    margin: 20px;
  }
}

@media screen and (max-width: 500px) {
  .burger-menu-line {
    width: 8vw;
  }

  .close-burger-menu {
    width: 100%;
  }

  .menu-active {
    margin: 0;
  }
}

