*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  font-family: "Nunito", sans-serif;
  background-color: var(--clr-back);
  color: var(--clr-txt);
  min-height: 100%;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.5;
}

button {
  outline: none;
  border: none;
  padding: 0;
  font-family: inherit;
  font-size: 100%;
  overflow: visible;
  text-transform: none;
  background-color: inherit;
}

button:hover,
a:hover {
  cursor: pointer;
}

a {
  color: inherit;
  text-decoration: inherit;
}

ol,
ul,
li {
  list-style: none;
}

.App {
  width: 100%;
  height: 100vh;
  margin: 0 auto;
  display: grid;
  grid-template-rows: 1fr 13fr 1fr;
  grid-column-gap: 1rem;
  grid-row-gap: 1rem;
  grid-template-areas:
    "h h h"
    "m m m"
    "f f f";
}

.main-container {
  grid-area: m;
  padding-top: 5vh;
}

.page-container {
  padding: 0 2rem;
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  justify-content: center;
  scroll-behavior: smooth;
}

@media screen and (max-width: 900px) and (min-height: 1023px) {
  .main-container {
    padding-top: 0;
  }
}

@media screen and (max-width: 800px) {
  .page-container {
    padding: 0 1rem;
  }
}

@media screen and (max-width: 300px) {
  .App {
    font-size: 0.85em;
  }

  .page-container {
    padding: 0;
    width: 90%;
    margin: 0 5%;
  }
}

